.album-card {
    background: #f0f2f5;
    border: 1px solid #e1e5eb;
    border-radius: 6px;
    box-shadow: 1px 1px 2px black rgb(0.6) , 0.1px 1px 3px black rgba(0.2);
    display: flex;
    flex-direction: column;
    height: 250px;
    margin: 0 25px 25px;
    padding-bottom: 16px;
    width: 220px;
  }
  
  .album-image {
    display: flex;
    height: 180px;
  }
  
  .album-image img {
    border-radius: 6px;
    flex-grow: 1;
    padding: 7px;
  }
  
  .artist-info {
    display: flex;
    flex-direction: column;
    font-family: Verdana , Geneva , Tahoma , sans-serif;
    margin-left: 10px;
  }
  
  .artist-info span {
    color: #333d4d;
    font-size: 16px;
    font-weight: 700;
  }
  
  .artist-info p {
    color: #3d495c;
    font-weight: 400;
  }