@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300;500;700&display=swap);
html {
  margin: 0px;
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0px;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: larger;
}
.login-page {
    background-color: #00DBDE;
    background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
  }
  
  .login-rectangle {
    align-content: center;
    align-self: center;
    display: flex;
    flex-direction: column;
  }
  
  .logo-trybe-tunes {
    align-self: center;
  }
  
  .form-rectangle {
    align-self: center;
    background-color: #D9AFD9;
    background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 50%, #ffffff 100%);
    border-radius: 10px;
    box-shadow: 2px 4px 4px #b1b0b0;
    display: flex;
    flex-direction: column;
    height: 314px;
    justify-content: center;
    margin-top: 100px;
    width: 697px;
  }
  
  .login-form {
    align-self: center;
    display: flex;
    flex-direction: column;
  }
  
  .login-input {
    background: white;
    border: 1px solid #e1e5eb;
    font-size: 16px;
    margin-bottom: 30px;
    max-height: 50px;
    padding: 12px;
    width: 519px;
  }
  
  .btn {
    align-items: center;
    background: #00e5b0;
    border: none;
    border-radius: 2px;
    box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
    color: white;
    display: flex;
    flex-direction: row;
    font-size: 1rem;
    height: 40px;
    justify-content: center;
    padding: 8px 32px;
  }
.header {
    display: flex;
    flex-direction: column;
    font-family: Epilogue , sans-serif;
}
  
.top-header {
    align-items: center;
    background-color: #FFDEE9;
    background-color: #08AEEA;
    background-color: #00DBDE;
    background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);
    display: flex;
    flex-direction: row;
    height: 100px;
    justify-content: space-between;
}
  
.logo-header {
    height: 96px;
    margin-left: 35px;
    margin-top: -30px;
    width: 120px;
}
  
.user-button {
    align-items: center;
    border-radius: 1px;
    box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.25);
    display: flex;
    font-weight: 600;
    height: auto;
    justify-content: center;
    margin-right: 50px;
    width: auto;
    color: white;
}
  
.navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
  
.link-nav {
    align-items: center;
    background: #f0f2f5;
    background-color: #D9AFD9;
    background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 50%, #ffffff 100%);
    color: #2fc18c;
    display: flex;
    font-size: 28px;
    font-weight: 700;
    height: 78px;
    justify-content: center;
    line-height: 29px;
    text-decoration: none;
    width: 1439px;
}
  
.link-nav:hover {
    background-color: #00DBDE;
    background-color: #08AEEA;
    background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);

    color: white;
}
  
.favorites-nav {
    margin-left: 5px;
    margin-right: 5px;
}
.album-card {
    background: #f0f2f5;
    border: 1px solid #e1e5eb;
    border-radius: 6px;
    box-shadow: 1px 1px 2px black rgb(0.6) , 0.1px 1px 3px black rgba(0.2);
    display: flex;
    flex-direction: column;
    height: 250px;
    margin: 0 25px 25px;
    padding-bottom: 16px;
    width: 220px;
  }
  
  .album-image {
    display: flex;
    height: 180px;
  }
  
  .album-image img {
    border-radius: 6px;
    flex-grow: 1;
    padding: 7px;
  }
  
  .artist-info {
    display: flex;
    flex-direction: column;
    font-family: Verdana , Geneva , Tahoma , sans-serif;
    margin-left: 10px;
  }
  
  .artist-info span {
    color: #333d4d;
    font-size: 16px;
    font-weight: 700;
  }
  
  .artist-info p {
    color: #3d495c;
    font-weight: 400;
  }
.search-page {
    display: flex;
    flex-direction: column;
  
  }
  
  .form-search {
    align-self: center;
    display: flex;
    justify-content: center;
    padding: 99px 0 112px;
  }
  
  .input-search {
    align-items: flex-start;
    border: 1px solid #3d495c;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    margin-right: 27px;
    padding: 12px 16px;
    width: 300px;
  }
  
  .search-button {
    align-items: center;
    background: #00e5b0;
    border-radius: 2px;
    color: white;
    display: flex;
    flex-direction: row;
    font-family: Verdana , Geneva , Tahoma , sans-serif;
    font-size: 16px;
    justify-content: center;
    padding: 8px 32px;
    width: 134px;
  }
  
  .album-list {
    align-self: center;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: auto;
    max-width: 1370px;
  
  }
  
  .link-album-card {
    text-decoration: none;
  }
  
  .span-albuns-found {
    color: #001813;
    font-family: Epilogue , sans-serif;
    font-size: 24px;
    font-weight: 300;
    align-self: left;
    display: flex;
    justify-content: left;
    margin-left: 18%;
    margin-bottom: 3%;

  }
  
  .div-not-found {
    align-self: center;
    color: #023031;
    font-family: Epilogue , sans-serif;
    font-size: 60px;
    font-weight: 300;
    margin-top: 200px;
  }
.music-page {
    display: flex;
    
    align-items: center;
    
  }
  
  .music-page .album-card {
    height: 280px;
    justify-content: space-between;
    margin-right: 100px;
    width: 270px;
  }
  
  .track-list-section {
    height: 25rem;
    overflow: scroll;
    width: 50rem;
  }
  
  .track-list {
    display: flex;
    flex-direction: column;
    margin-bottom: 70%;
    width: 85%;
  }
  
  .track-section {
    align-items: center;
    border-top: 1px solid #e1e5eb;
    display: inline-block;
    justify-content: space-between;
    padding: 10px;
  }
  
  .track-name {
    margin-right: 10px;
  }

  .album-name{
    display: inline-block;
    vertical-align: middle;
  }

  .stuck {
    position: absolute;
    top: 200px;
    right: 0;
}


.track-list-fav {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 80%;
}


.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  header {
    margin-bottom: 30px;
  }
  
  .col-md-4 {
    margin-bottom: 30px;
  }
  
  .card {
    padding: 30px;
    border: solid 1px rgba(0, 0, 0, 0.08);
    cursor: pointer;
  }
  .card:hover {
    box-shadow: 0 0 12px 3px rgba(0, 0, 0, 0.08);
  }
  .card-title {
    margin-top: 20px;
  }
  .card-text {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
  }
  .card-text .fa {
    font-size: 26px;
  }
  .avatar {
    width: 140px;
    margin: auto;
  }
  .avatar img {
    border: solid 6px transparent;
    border-radius: 50%;
  }
  .card:hover .avatar img {
    border-color: rgb(99, 204, 131);
  }
  
  .btn {
    border-radius: 25px;
  }
  
  footer p {
    color: rgba(0, 0, 0, 0.6);
  }
  
  footer p a,
  footer p a:hover {
    color: rgb(99, 204, 131);
  }
  
  @media (min-width: 1200px) {
    .container {
      max-width: 1040px;
    }
  }
  


.mainDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rebeccapurple;
}

.form-box h5{
    font-size: 35px ;
    text-align: center;
    color: #F55A00;
    margin: 0 0 .5vh 0;
}

.form-box{
    box-sizing: border-box;
    border: 1px solid lightblue;
    width: 30%;
    margin-left: 35%;
    background-color: aliceblue;
   
}
.form-box label{
    display: block;
    margin-bottom: 50px;
    font-size: 20px;
    
}

.form-box input, textarea{
    display: block;
    
    padding: .5rem .8rem .5rem .8rem;
    margin: .9vw 0 ;
    border:0;
    border-radius: 5px;
    font-size: 20px;
}

.form-box textarea{
    height: 15vh;
}

.form-box input{
    display: block;
    width: 90%;
    padding: .5rem .8rem .5rem .8rem;
    margin: .9vw 0 ;
    border:0;
    border-radius: 5px;
    font-size: 20px;
}

.form-box textarea{
    display: block;
    width: 90%;
    padding: .5rem .8rem .5rem .8rem;
    margin: .9vw 0 ;
    border:0;
    border-radius: 5px;
    font-size: 20px;
}


.form-box textarea{
    height: 10vh;
}

.profile-image{
    margin-bottom: 50px;
    margin-left: 80px;
}
